@import "../../../sass/mixins/all";

* {
    box-sizing: border-box;
}

.wp-block-anitian-media-text {
    background: #1870b8;
    position: relative;
    padding: 60px 0;
    .media-text-v3__image__background {
        position: absolute;
        background-size: calc(100% + 2px) calc(100% + 2px);
        background-repeat: no-repeat;
        background-position: 50%;
        overflow: hidden;
        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        transform: scale(-1,1);
    }
    .container {
        width: 100%;
        max-width: 1200px;
        margin-right: auto;
        margin-left: auto;
        padding: 0 15px;
        .content-is-center {
            align-items: center;
        }
        .media-text-v3__wrapper {
            display: flex;
            gap: 4%;
            position: relative;
            .media-text-v3__image {
                width: 50%;
                img {
                    max-width: 65%;
                    display: block;
                    margin: 0 auto;
                    border-radius: 10px 0 10px 10px;
                    box-shadow: 0px 12px 18px -6px rgba(0,0,0,.3);
                    height: auto;
                }
            }
            .media-text-v3__content {
                width: 50%;
                padding: 40px 0;
                .media-text-v3__title {
                    position: relative;
                    font-weight: 600;
                    margin: 0 0 30px;
                    line-height: 1.2em;
                    font-size: 35px;
                    &::before{
                        display: inline-block;
                        content: "";
                        background-image: url(data:image/svg+xml;base64,);
                        background-size: 28px 28px;
                        height: 28px;
                        width: 28px;
                        margin-right: 10px;
                        vertical-align: middle;
                    }
                }
                .media-text-v3__desc {
                    margin: 0 0 30px;
                    font-size: 16px;
                }
                .hubspot-btn {
                    margin: 40px 0 0;
                    .thar-three {
                        border-radius: 50px;
                        padding-left: 25px;
                        padding-right: 25px;
                        padding-top: 10px;
                        padding-bottom: 10px;
                        color: #fff;
                        font-size: 18px;
                        font-weight: 500;
                        background: #f75227;
                        cursor: pointer;
                        display: block;
                        position: relative;
                        border: 2px solid #f75227;
                        transition: all .4s cubic-bezier(.42,0,.58,1);
                        width: max-content;
                        a{
                            text-decoration: none !important;
                            color: inherit;
                        }
                        &:hover{
                            color: #f75227;
                            background: #fff;
                        }
                    }
                }
            }
        }
    }
}